const contentWidthLaptop = 850;
const theme = {
  // named colors:
  brand: '#FC5B5B',
  accent: 'white',
  accentDark: '#35495E',
  lightGrey: '#FC5B5B1A',
  darkGrey: '#91a2a3',
  ink: 'black',
  errorRed: '#FF6666',
  codeEditBlue: '#2973b7',
  codeEditGreen: '#42b983',

  // content width:
  contentWidthLaptop: `${contentWidthLaptop}px`,
  widthLaptop: `${contentWidthLaptop / 2 * 3}px`,
  sitePadding: '25px'

};

export default theme;



